* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

body {
  font-family: montserrat;
}

nav {
  background-color: #0082e6;
  height: 80px;
  width: 100%;
  font-family: "Kalam";
  position: fixed;
  top: 0;
  z-index: 1;
}

label.logo {
  color: white;
  font-size: 35px;
  line-height: 80px;
  padding: 0 100px;
  font-weight: bold;
}

nav ul {
  float: right;
  margin-right: 20px;
}

nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
}
nav ul li a {
  color: white;
  font-size: 17px;
  text-transform: uppercase;
  border-radius: 3px;
  /* padding: 7px 13px; */
  line-height: 10px;
}

a.active,
a:hover {
  /* background: #1b9bff; */
  transition: 0.5s;
  text-decoration: none;
}

.checkbtn {
  font-size: 30px;
  color: white;
  line-height: 80px;
  float: right;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}

@media (max-width: 952px) {
  label.logo {
    font-size: 30px;
    padding-left: 50px;
  }
  nav ul li a {
    font-size: 16px;
  }
}

@media (max-width: 858px) {
  .checkbtn {
    display: block;
  }
  ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #2c3350;
    top: 80px;
    left: -100%;
    text-align: center;
    transition: all 0.5s;
  }
  nav ul li {
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }
  nav ul li a {
    font-size: 20px;
  }
  a:hover,
  a.active {
    background: none;
    color: #0082e6;
  }
  #check:checked ~ ul {
    left: 0;
  }
}

@media screen and (min-width: 300px) and (max-width: 400px) {
  .logo {
    display: none;
  }
  ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  .mr-auto {
    max-height: 470px;
  }
}

@media screen and (min-width: 400px) and (max-width: 600px) {
  ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .mr-auto {
    max-height: 470px;
  }
}
